import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HappySettingsComponent } from './happy-settings.component';

const routes: Routes = [
    {
        path: '',
        component: HappySettingsComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class HappySettingsRoutingModule {
}
